.HomeSection{
    padding-top: 138px;
    padding-bottom: 220px;
    height: fit-content;
}

.userInput{
    display: flex;
    justify-content: right;
    align-items: start;
    gap: 15px;
    margin-bottom: 36px;
    margin-right: 30px;
}

.userInput img{
    width: 57px;
    border-radius: 50%;
}
.userInput p{
    padding: 20px;
    background-color: whitesmoke;
    height: max-content;
    width: 25%;
    border-radius: 10px;
}
.response{
    display: flex;
    justify-content: left;
    align-items: start;
    gap: 15px;
    margin-left: 30px;
}
.response p{
    padding: 20px;
    background-color: whitesmoke;
    height: max-content;
    width: 60%;
    border-radius: 10px;
    text-align: justify;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    line-height: 27px;
    font-weight: 500;
}
.response p::-webkit-scrollbar{
    display: none;
}
.gemeniLogo{
    width: 57px;
}
.allData{
    display: flex;
    flex-direction: column;
}
.Empty{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.logo{
    width: 230px;
    border-radius: 50%;

}
.loading{
    display: flex;
    gap: 15px;
    align-items: center;
    margin-left: 20px;
}
.loading img{
    width: 57px;

}

@keyframes showHide {
    0%, 100% {
      opacity: 0; /* Hidden */
    }
    50% {
      opacity: 1; /* Fully visible */
    }
  }

.loading div{
    padding: 20px;
    background-color: whitesmoke;
    text-align: center;
    width: 10%;
    border-radius: 10px;
    animation: showHide 1.5s infinite; /* 2 seconds per cycle, infinite loop */
  transition: opacity 0.5s ease-in-out;
}


@media (min-width: 270px) and (max-width: 450px) {
    .userInput p{
        width: 70%;
        padding: 15px;
        text-align: center;
    }
    .userInput img{
        width: 40px;
    }
    .userInput{
        align-items: center;
        margin-bottom: 20px;
        margin-right: 10px;
    }
    .response{
        margin-left: 10px;
        gap: 3px;
    }
    .response p{
        width: 80%;
    }
    .response img{
        width: 30px;
    }
    .loading img{
        width: 30px;
    }
    .loading div{
        width: 35%;
        padding: 9px;
        text-align: center;
    }
    .Empty img{
        width: 150px;
    }
    .Empty h1{
        font-size: 25px;
        
    }
    .Empty{
        gap: 20px;
    }
}
@media (min-width: 451px) and (max-width: 770px) {
    .userInput p{
        width: 70%;
        padding: 15px;
        text-align: center;
    }
    .HomeSection{
        height: fit-content;
    }
    .userInput img{
        width: 40px;
    }
    .userInput{
        align-items: center;
        margin-bottom: 20px;
        margin-right: 10px;
    }
    .response{
        width: 100%;
        margin-left: 10px;
        gap: 3px;
    }
    .response p{
        width: 70%;
    }
    .response img{
        width: 30px;
    }
    .loading img{
        width: 30px;
    }
    .loading div{
        width: 35%;
        padding: 9px;
        text-align: center;
    }
    .Empty img{
        width: 150px;
    }
    .Empty h1{
        font-size: 25px;
        
    }
    .Empty{
        gap: 20px;
    }

}
