.barSection{
    background-color: black;
    height: 100%;
    width: 315px;
    animation: bars 0.2s ease 0s 1 forwards;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.tabs h1{
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 10px;
}
.searchHistory{
    width: 280px;
    height: fit-content;
    padding: 5px;
    background-color: rgba(245, 245, 245, 0.774);
    color: black;
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    list-style: none;
    border-radius: 12px;
    margin-top: 17px;
    cursor: pointer;}
.barSection{
    overflow-y: auto;
}
.searchHistory:hover{
    background-color: whitesmoke;
}
.historyContainer{
    height: fit-content;
}
.historyContainer{
    padding-bottom: 30px;

}

@keyframes bars {
    from{
        width: 0px;
    }
    to {
        width: 320px;
    }
    
}