.navbar{
    background-color: black;
    height: 60px;
    display: flex;
    align-items: center;
    border-bottom: 2px solid whitesmoke;
}
.navbarItems{
    color: white;
    display: flex;
    margin-left: 30px;
    gap: 40px;
}
.barsLogo{
    display: none;
    cursor: pointer;
}

@media  (min-width: 280px) and (max-width: 800px){
    .barsLogo{
        display: inline-flex;
    }
}
